@use './../base/variables' as breakpoints;

.call-to-action {
    display: flex;
    gap: 5rem;
    align-content: center;
    align-items: center;
    justify-content: space-between;

    &-module {
        padding: 4rem 0;
    }

    &__text-content-wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }

    &__heading {
        margin: 0;
    }

    &__desc {
        margin-top: .8rem;
    }

    &__btn {
        margin-top: .9rem;
    }

    &__images-wrapper {
        position: relative;

        @media screen and (max-width: breakpoints.$tablet) {
            width: 75%;
        }

        @media screen and (max-width: breakpoints.$phone) {
            width: 50%;
        }
    }

    &__editor {
        &:hover {
            & + .call-to-action__floating-editor {
                left: 0;
                transition: left var(--transition-swing);
            }
        }

        @media screen and (max-width: breakpoints.$tablet) {
            width: 100%;
        }
    }

    &__floating-editor {
        position: absolute;
        top: 1.5rem;
        left: -25%;
        pointer-events: none;
        transition: left var(--transition-swing);
    }

    @media screen and (max-width: breakpoints.$tablet) {
        flex-wrap: wrap-reverse;
        justify-content: center;
    }
}
